import React, {FC, useEffect, useState} from 'react';
import styles from './Install.module.scss';

export const Install: FC = () => {
  const [displayMode, setDisplayMode] = useState('unknown');
  // @ts-ignore
  const isStandalone = window.matchMedia('(display-mode: fullscreen)').matches || window.navigator.standalone;

  useEffect(() => {
    window.matchMedia('(display-mode: fullscreen)').addEventListener('change', (evt) => {
      if (evt.matches) {
        setDisplayMode('standalone');
      } else {
        setDisplayMode('browser');
      }
    });
  });

  return (
    <div className={styles.install}>
      <div className={styles.installContent}>
        <div className={styles.icon}/>
        <h1 className={styles.title}>You’re just two taps away.</h1>
        <div>
          For the best experience add the app to your home screen:
          <ol>
            <li>Open this website in Safari.</li>
            <li>Tap the ‘Share’ button.</li>
            <li>Scroll down and tap ‘Add to Home Screen.’</li>
            <li>Tap add button in the top right corner.</li>
            <li>Done, the app is on your home screen!</li>
          </ol>
          <h1>
            {isStandalone ? 'Standalone' : 'Not standalone'}
          </h1>
          <p>
            <h3>
              Mode: {displayMode}
            </h3>
          </p>
        </div>
      </div>s
    </div>
  );
};
